import React, { useState, useEffect } from "react";
import { Table, Button, Modal, Input, Form, Space } from "antd";
import { CopyOutlined, DeleteOutlined, PlusOutlined } from "@ant-design/icons";

import styles from "./styles.module.css";
import axios from "axios";

async function extractDetails(response) {
  const enhancedData = await Promise.all(
    response.map(async (item) => {
      // Extract procedures (all keys with true values except injections)
      const procedures = Object.keys(item).filter((key) => item[key] === true);

      // TODO: Define your criteria for 'Dispensed Injections'
      // For now, I'm assuming the same as procedures
      const dispensedInjections = procedures;

      // Fetching patient name using patient ID
      let patientName = "Not Found"; // Default if not found
      try {
        const patientResponse = await axios.get(
          `/patient-profiles/by-customer/${item.PatientId}`
        );
        patientName = patientResponse.data.profile["Full Name"];
      } catch (err) {
        console.error(`Error fetching patient with ID ${item.PatientId}:`, err);
      }

      return {
        "Patient Name": patientName,
        Room: item.room,
        Procedures: procedures.join(", "),
        "Dispensed Injections": dispensedInjections.join(", "),
        Labs: item.labs.note,
        Note: item.note,
        ID: item.PatientId,
        DB_ID: item._id,
      };
    })
  );

  return enhancedData;
}

// Load treatments and pass them to extractDetails on page load
window.onload = function () {
  axios
    .get("/treatments/today")
    .then((response) => {
      const details = extractDetails(response.data);
      console.log(details);
      // Here, you can pass 'details' to any other functions, or render them on the page
    })
    .catch((error) => {
      console.error("Error fetching treatments:", error);
    });
};

const TodayView = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      // <-- Declare an async function inside useEffect
      try {
        const response = await axios.get(
          "/treatments/today"
        );
        const details = await extractDetails(response.data); // <-- Await the extraction
        const mappedDetails = details.map((detail, index) => ({
          key: index + 1,
          ...detail,
          patientName: detail["Patient Name"],
          room: detail["Room"],
          procedures: detail["Procedures"],
          dispensedInjections: detail["Dispensed Injections"],
          labs: detail["Labs"],
          note: detail["Note"],
          id: detail["ID"],
        }));
        setData(mappedDetails);
      } catch (error) {
        console.error("Error fetching treatments:", error);
      }
    };

    fetchData(); // <-- Call the async function immediately
  }, []);

  const [isModalVisible, setIsModalVisible] = useState(false);
  const [currentItem, setCurrentItem] = useState(null);
  const [form] = Form.useForm();

  const deleteRow = (key) => {
    setData((prevData) => prevData.filter((item) => item.key !== key));
  };

  const openModal = (item) => {
    setCurrentItem(item);
    form.setFieldsValue(item); // Set initial values in the form
    setIsModalVisible(true);
  };

  const handleOk = () => {
    const updatedValues = form.getFieldsValue();
    if (currentItem) {
      setData((prevData) =>
        prevData.map((item) =>
          item.key === currentItem.key ? { ...item, ...updatedValues } : item
        )
      );
    } else {
      // Assign a new key for the new row and add it to the data
      setData((prevData) => [
        ...prevData,
        { ...updatedValues, key: Date.now() },
      ]);
    }
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const columns = [
    {
      title: "Actions",
      dataIndex: "actions",
      render: (_, item) => (
        <Space>
          <Button icon={<CopyOutlined />} onClick={() => openModal(item)} />
          <Button
            icon={<DeleteOutlined />}
            onClick={() => deleteRow(item.key)}
          />
        </Space>
      ),
    },
    {
      title: "Patient Name",
      dataIndex: "patientName",
    },
    {
      title: "Room",
      dataIndex: "room",
    },
    {
      title: "Procedures",
      dataIndex: "procedures",
    },
    {
      title: "Dispensed Injections",
      dataIndex: "dispensedInjections",
    },
    {
      title: "Labs",
      dataIndex: "labs",
    },
    {
      title: "Note",
      dataIndex: "note",
    },
    {
      title: "ID",
      dataIndex: "id",
    },
  ];

  return (
    <div className={styles.todayViewContainer}>
      <h1 className={styles.heading}>Patients Today</h1>
      <div className={styles.tableContainer}>
        <Button
          icon={<PlusOutlined />}
          type="primary"
          style={{ marginBottom: "16px" }}
          onClick={() => openModal()}
        >
          Add Row
        </Button>
        <Table columns={columns} dataSource={data} pagination={false} />
      </div>

      <Modal
        title={currentItem ? "Edit Patient Data" : "Add New Patient"}
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Form layout="vertical" form={form}>
          <Form.Item label="Patient Name" name="patientName">
            <Input />
          </Form.Item>
          <Form.Item label="Room" name="room">
            <Input />
          </Form.Item>
          <Form.Item label="Procedures" name="procedures">
            <Input />
          </Form.Item>
          <Form.Item label="Dispensed Injections" name="dispensedInjections">
            <Input />
          </Form.Item>
          <Form.Item label="Labs" name="labs">
            <Input />
          </Form.Item>
          <Form.Item label="Note" name="note">
            <Input.TextArea />
          </Form.Item>
          <Form.Item label="ID" name="id">
            <Input />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default TodayView;
