import React, { useState } from "react";
import styles from "./styles.module.css";  
import { Row, Col } from 'antd';
import logo from "../../Assets/Logo.png";
import { useNavigate } from "react-router-dom";
import axios from "axios"; 
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { EyeOutlined, EyeInvisibleOutlined } from '@ant-design/icons';
import Cookies from "js-cookie";

const Login = () => {
  // State to hold the userName and password values
  const navigate = useNavigate();
  const [userName, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
 
  
  const handleLogin = async() => {
    setError('');
    if ( !userName.trim() || !password.trim() ) {
      return setError('All fields are required.');
    }

    try { 
      const response = await axios.post(`/auth/signin`, { 
        username: userName,
        password
      }, 
    );
      
      if (response.status === 200) {
        toast.success(response.data.message);
        // Cookies.set('admin_cookie', response.data.userId, { expires: 1, path: "/", });  
        setTimeout(() => {
          navigate("/treatmentview");
        }, 2000); 
      } 
    } catch (error) {
      if (error.response && error.response.status === 401){
        console.log(error.response.data.message);
        toast.error(error.response.data.message);
      }
      if (error.response && error.response.status === 409){
        console.log(error.response.data.message);
        setError(error.response.data.message);
      }
      console.error("Signup Error:", error);
    }  
  }; 
  
  const handleSignUp = () => { 
    navigate("/signup");
  };


  return (
    <> 
      <Row className={styles.login_container} align="middle" justify="center">
        <Col xs={22} sm={16} md={12} lg={8} xl={6}>
          <div className={styles.logo}>
              <img className={styles.img1} src={logo} alt="Logo" />
          </div>
          <div className={styles.login_box}>
            <div className={styles.login_header}>Log In to your account</div> 

            <div className={styles.login_label}>Email</div>
            <input 
              type="userName" 
              className={styles.login_input} 
              value={userName}
              onChange={(e) => setUserName(e.target.value)}
              placeholder="Enter your userName"
            />

            <div className={styles.login_label}>Password</div>
            <input 
              type="password" 
              className={styles.login_input} 
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Enter your password"
            />
            
            {error && <div className={styles.error_message}>{error}</div>}
            
            <div className={styles.login_btn} onClick={handleLogin}>Log In</div>

            <div className={styles.login_signup}>
              New Here? <span className={styles.login_signup_highlight} onClick={handleSignUp}>Sign Up</span>
            </div>
          </div>
        </Col>
        <ToastContainer position="top-right" autoClose={3000} hideProgressBar={false} newestOnTop closeOnClick rtl={false} pauseOnFocusLoss draggable pauseOnHover />
      </Row>
    </>
  );
};

export default Login;